import React, { useContext, useEffect, useState } from 'react'
import styled from 'styled-components'
import { BrowserContext } from '../../store/BrowserContext';

const StyledComponent = styled.div`
:root {
  --main-blk-color: #25282b;
  scroll-behavior: smooth;
}
.mySlides {
  display: none;
}

.mySlides img {
  margin:0!important;
}

.gallery-container {
  position: relative;
  border:5px solid var(--main-blk-color);
}

.caption-container {
  text-align: center;
  background-color: var(--main-blk-color);
  padding: 2px 16px;
}

imgSlide img {
    display:block;
}


.goLeft,
.goRight {
  cursor: pointer;
  position: absolute;
  top: 50%;
  width: auto;
  padding: 16px;
  margin-top: -50px;
  color: white;
  font-weight: bold;
  font-size: 20px;
  border-radius: 0 3px 3px 0;
  user-select: none;
  -webkit-user-select: none;
  background-color: rgba(0, 0, 0, 0.6);
  direction: ltr;
}
.goLeft{
  left: 0;
}


.goRight {
  right: 0;
  border-radius: 3px 0 0 3px;
}


.goLeft:hover,
.goRight:hover {
  background-color: rgba(0, 0, 0, 0.8);
}

.numbertext {
  color: #f2f2f2;
  font-size: 12px;
  padding: 8px 18px;
  position: absolute;
  border-radius: 5px;
  top: 10px;
  left: 10px;
  background-color: rgba(0, 0, 0, 0.8);
}

#caption {
  color: white!important;
  margin: 1rem auto;
}

.container, p, h1 {
  width:50%;
  margin:auto;
}

p {
  font-size:20px;
  padding:1rem 0;
}

h1 {
  padding:2rem 0;
  font-size: 40px;
  text-align:center;
}
.fullscreen-display-hide {
  display:none;
}

.fullscreen-display-hide i {
  display:none;
}

.fullscreen-display-show {

  position:fixed; /* takes up whole viewport */
  left:0; /*snaps div to left*/
  top:0;/*snaps div to left*/
  display:flex;
  z-index:1000;
  width: 100%;
  height:100%;
  background-color:rgba(0, 0, 0, 0.95);
}

.fullscreen-display-show  img {
  height: fit-content;
  margin:auto!important
}
.fullscreen-display-show i {
  display: block;
  font-size: 1rem;
  color:white;
  cursor:pointer;
  padding: 10px 15px;
  background-color:#dc3545!important;
  height: fit-content;
  border-radius: 10px;
  margin:0.5rem;
  position:absolute;
  right:0;
}
.imageSlide{
  max-height: 80%;
  background-color: var(--white);
}

`

const toggleFullscreenImage = (imageName = null) => {
  var fullscreenDisplayDiv = document.getElementById("fullscreen-display");
  var img = fullscreenDisplayDiv.getElementsByTagName("div");
  var close = fullscreenDisplayDiv.getElementsByTagName("i");
  console.log(imageName)
  
  if(imageName != null) {
      img[0].setAttribute("src", imageName);
      close[0].classList.remove('d-none')
      close[0].classList.add('d-block')
      fullscreenDisplayDiv.className = "fullscreen-display-show";
      window.document.body.style.overflow = "hidden";
  }
  else {
      img[0].setAttribute("src", " ");
      close[0].classList.remove('d-block')
      close[0].classList.add('d-none')
      fullscreenDisplayDiv.className = "fullscreen-display-hide";
      window.document.body.style.overflow = "auto";
  }
};



const ImagesGallery = ({images_list}) => {
  const [selectedImage, setSelectedImage] = useState(1)
  const {language} = useContext(BrowserContext).browserData
  const decImagesList = ()=>{
    setSelectedImage((prevState)=> prevState === 1 ? images_list.length : prevState -1)
  }
  const incImagesList = ()=>{
    setSelectedImage((prevState)=> prevState === images_list.length ? 1 : prevState + 1)
  }
  let isGoogleAd = images_list[selectedImage-1] === 'google-ads'
  return (
    <StyledComponent>    
      <div id="fullscreen-display" className="fullscreen-display-hide">
        <div id='fullscreen-display__img google-ads' style={{height: '100vh', width: '100vw', backgroundColor: isGoogleAd ? 'var(--theme)' : 'inherit', backgroundImage: isGoogleAd ? 'none' :  `url('${images_list[selectedImage-1]}') `, backgroundRepeat: 'no-repeat', backgroundPosition: 'center', backgroundSize: 'contain', margin: 'auto'}}></div>
        <i className="fas fa-times d-none" onClick={()=>toggleFullscreenImage()}></i>
        <div className="numbertext border">{selectedImage} { language ==='ar' ? '\\' : '/'} { images_list.length }</div>
        <span className="goLeft" onClick={()=>setSelectedImage((prevState)=> prevState === 1 ? images_list.length : prevState -1)}>❮</span>
        <span className="goRight" onClick={()=>setSelectedImage((prevState)=> prevState === images_list.length ? 1 : prevState + 1)} >❯</span>
      </div>
      <div className="gallery-container" id='imagesContainer' style={{width: '100%', position: 'relative', maxHeight: 'calc(100vh - 120px)' }}>
        <div className={`imageSlide d-block`}>
        <div className='aspect-ratio-1 m-auto w-100' style={{width: '100%', position: 'relative', maxHeight: 'calc(100vh - 120px)', backgroundColor : isGoogleAd ? 'bg-color-theme': 'unset', backgroundImage: isGoogleAd ? 'none' :  `url('${images_list[selectedImage-1]}')`,  backgroundRepeat: 'no-repeat', backgroundPosition: 'center', backgroundSize: 'contain'}}>
          <div className="numbertext">{selectedImage} { language ==='ar' ? '\\' : '/'} { images_list.length }</div>
          <div className={`w-100 h-100 cursor-pointer ${isGoogleAd && 'bg-color-theme google-ads'}`} onClick={(e)=>{toggleFullscreenImage(`${ images_list[selectedImage-1] }`)}}/>
          <span className="goLeft" onClick={()=>(language === 'ar' ? incImagesList(): decImagesList())}>❮</span>
          <span className="goRight" onClick={()=>(language === 'ar' ? decImagesList(): incImagesList())}>❯</span>
        </div>
        </div>          
      </div>
    </StyledComponent>
  )
}

export default ImagesGallery