import React, { useEffect, useRef, useState } from 'react'
import CommentModal from '../../Components/UI/CommentModal'
import axios from 'axios'
import { useContext } from 'react'
import { Loader } from '../../Components/Loader'
import { UserContext } from '../../store/UserContext'
import { Message } from '../../Components/Message'
import { useParams } from 'react-router-dom'
import { useIsVisibleOnce } from '../../utils'
import CommentsLoading from './ArticlePage/CommentsLoading'
import CommentCard from './ArticlePage/CommentCard'
import { TranslationContext } from '../../store/TranslationContext'


const CommentSection = () => {
    const commentSectionRef = useRef();
    const isCommentSectionVisible = useIsVisibleOnce(commentSectionRef);
    const { articleSlug } = useParams();
    const slugList = articleSlug.split('-')
    const articleId = slugList[slugList.length - 1]
    const {userData} = useContext(UserContext)
    const [loading, setLoading] = useState(true)
    const [comments, setComments] = useState([])
    const commentsIdList = useRef([])
    const [commentInput, setCommentInput] = useState('')
    const [selectedComment, setSelectedComment] = useState(null)
    const openCommentModal = useRef()
    const [isNext, setIsNext] = useState(false)
    const [newCommentsLoading, setNewCommentsLoading] = useState(false)
    const getComments =()=>{
        axios.post(
            `/api/article/get-article-comments/`,
            {
                'seen-comments' : JSON.stringify(commentsIdList.current),
                'article-id' : articleId
            },
            {
                headers : {
                  "Content-Type": "multipart/form-data",
                }
            }
        ).then((respose)=>{
            respose.data[0].forEach((elem)=>{
                commentsIdList.current = [...commentsIdList.current, elem.id]
            })
            setComments((prevState)=>[...prevState, ...respose.data[0]])
            setLoading(false)
            setNewCommentsLoading(false)
            setIsNext(respose.data[1])
        }).catch((error)=>{
            setLoading(false)
            setNewCommentsLoading(false)
            setComments((prevState)=>prevState || [])

        })
        
    }
    useEffect(()=>{
        if (isCommentSectionVisible){
            getComments()
        }
    }, [articleId, isCommentSectionVisible])

    const [createCommentLoading, setCreateCommentLoading] = useState(false)
    const [errorCreatingComment, setErrorCreatingComment] = useState(false)
    const createComment = ()=>{
        setCreateCommentLoading(true)
        axios.post(
            '/api/article/create-comment/',
            {text : commentInput,
            articleId : articleId,
            },
            {
                headers : {
                  "Content-Type": "multipart/form-data",
                  Authorization: `Bearer ${userData.token}`
                }
            }
            ).then((response)=>{
                setComments((prevState)=>{
                    return [...prevState, response.data]
                });
                setCreateCommentLoading(false)
                setCommentInput('')
            }).catch((error)=>{
                setErrorCreatingComment(error)
                setCreateCommentLoading(false)
            })
        
    }
    let count = 0
    const {translate} = useContext(TranslationContext)
    return (
        <>
            <section className="p-2 col-12 mx-auto" >
                <div className="col-12 p-3 card">
                        <div>
                            <div className='d-flex flex-column'>
                                { userData.id &&  <div>
                                    <textarea className='form-control my-3 resize-none' value={commentInput} maxLength={255} onChange={(e)=>{ setCommentInput(e.target.value) }} rows={3}></textarea>
                                    { errorCreatingComment && <Message variant='danger' removeAlert={()=>setErrorCreatingComment(null)}>{ errorCreatingComment.message && errorCreatingComment.response.data.detail }</Message> }  
                                    <div className='d-flex flex-row-reverse'>
                                        <button onClick={createComment} className='btn btn-success d-flex gap-2' disabled={ commentInput.trim().length === 0 || createCommentLoading }>{translate('Comment')}{ createCommentLoading ? <Loader diam={20}/> : '' }</button>
                                    </div>
                                </div>}
                                
                                {(loading) && 
                                    <div>
                                        <h3 className='color-theme mt-3'>{translate('Comment section')}</h3>
                                        <div ref={commentSectionRef}></div>
                                        <div className='mt-3 p-2 d-flex  flex-row overflow table-responsive'>
                                            {Array.from({length: 10}, (_, x) => x).map((i)=>{ return <CommentsLoading key={i}/>})}
                                        </div>
                                    </div>
                                }
                                {comments.length > 0 && 
                                <>  
                                    <h3 className='color-theme mt-3'>{translate('Comment section')}</h3>
                                    <div className='mt-3 d-flex  p-2 flex-row overflow table-responsive'>
                                        {comments.sort((a, b) => b.id - a.id).map((comment)=>{
                                            count +=1
                                            return (<React.Fragment key={comment.id}>
                                                {count % 7 === 0 && <div className='mx-1 card mb-4 col-10 col-sm-7 col-md-5 col-lg-4 col-xl-3 p-2 bg-color-theme google-ads'></div>}
                                                <CommentCard comment={comment} setSelectedComment={setSelectedComment} openCommentModal={openCommentModal}/>
                                            </React.Fragment>)
                                        })}
                                        {isNext && 
                                            <button disabled={newCommentsLoading} className={`border ${ newCommentsLoading ? '' : 'border-black'} rounded-circle align-self-center p-3 mx-4 mb-3 aspect-ratio-1 d-flex align-items-center`} onClick={()=>{setNewCommentsLoading(true); getComments()}}>
                                                <i className="fa-solid fa-plus fa-2xl"></i>
                                            </button>
                                        }
                                    </div>
                                    <CommentModal setComments={setComments} comment={selectedComment} ref={ openCommentModal } />
                                </>
                                }
                                {!loading && comments.length === 0 && 
                                    <div className='my-5'><h3 className='my-5 text-center' >{translate('No comments yet')}</h3></div>
                                }
                            </div>
                        </div>
                </div>
            </section>
        </>
        
  )
}

export default CommentSection