import React from 'react'

const CommentsLoading = () => {
  return (
    <div className='mx-1 card mb-4 col-10 col-sm-7 col-md-5 col-lg-4 col-xl-3 p-2'>
        <div className='d-flex align-items-center '>
            <span className="m-2 rounded-circle" style={{width : '50px', height : '50px', backgroundColor : 'var(--grey)'}}></span>
            <div className='d-flex flex-column gap-2 flex-grow-1'>
                <span className='rounded' style={{maxWidth : 100, height : 20, backgroundColor : 'var(--grey)'}}></span>
                <span className='rounded' style={{maxWidth : 150, height : 20, backgroundColor : 'var(--grey)'}}></span>
            </div>
        </div>
        <span className='rounded' style={{height : 67, backgroundColor : 'var(--grey)'}}></span>
    </div>
  )
}

export default CommentsLoading