import React, { useEffect, useState } from 'react'
import ImagesGallery from '../../Components/UI/ImagesGallery'
import { Link, useParams } from 'react-router-dom'
import axios from 'axios'
import { useContext } from 'react'
import { BrowserContext } from '../../store/BrowserContext'
import { UserContext } from '../../store/UserContext'
import { Message } from '../../Components/Message'
import ArticleLoading from './ArticleLoading'
import ArticleParameters from './ArticlePage/ArticleParameters'
import TimeElapsed from '../../Components/TimeElapsed'
import { TranslationContext } from '../../store/TranslationContext'

const ArticleInfoSection = ({setError, setBrandName, setTitle}) => {
    const { articleSlug} = useParams();
    const username = useParams().username.replace('@', '')
    const slugList = articleSlug.split('-')
    const articleId = slugList[slugList.length - 1]
    const {userData} = useContext(UserContext)
    const [data, setData] = useState(null)
    const [loading, setLoading] = useState(true)
    const [likeLoading, setLikeLoading] = useState(false)
    const [likingError, setLikingError] = useState(null)
    const {language} = useContext(BrowserContext)

    const [userLikeDislike, setUserLikeDislike] = useState([false, false])
    const [likesCount, setLikesCount] = useState(0)
    const [dislikesCount, setDislikesCount] = useState(0)
    const config = userData.id ? {
        headers : {
            Authorization : 'Bearer ' + userData.token
        }
    }: {}
    useEffect(()=>{
        const getArticleData = async()=>{
            try{
              setLoading(true)
              const response = await axios.get(
                `/api/article/get-article/?username=${ username.toLocaleLowerCase() }&id=${ articleId }`,
                config
                )
              setBrandName(response.data.info.brand_name)
              setTitle(response.data.info.title)
              setData(response.data)
              setLoading(false)
              setUserLikeDislike([response.data.is_liker, response.data.is_disliker])
              setLikesCount(response.data.likes_count)
              setDislikesCount(response.data.dislikes_count)
            }catch(error){
              setError(error)
              setLoading(false)
            }
        }
        getArticleData()
        }, [articleId])
        
    const likeArticle =()=>{
        if (userData.id){
            setLikeLoading(true)
            axios.post(
            '/api/article/like-article/',
            {'article-id' : data.id},
            {
                headers : {
                "Content-Type": "multipart/form-data",
                Authorization: `Bearer ${userData.token}`
                }
            }
            ).then((response)=>{
            setLikeLoading(false)
            userLikeDislike[0] ? setLikesCount(prevState=>prevState-1) : setLikesCount(prevState=>prevState+1)
            if(userLikeDislike[1])  setDislikesCount(prevState=>prevState-1)
            setUserLikeDislike((prevState)=>[!prevState[0], false])

            }).catch((error)=>{
            setLikeLoading(false)
            setLikingError(error)
            })
        }
    }

    const dislikeArticle =()=>{
        if (userData.id){
            setLikeLoading(true)
            axios.post(
            '/api/article/dislike-article/',
            {'article-id' : data.id},
            {
                headers : {
                "Content-Type": "multipart/form-data",
                Authorization: `Bearer ${userData.token}`
                }
            }
            ).then((response)=>{
            setLikeLoading(false)
            userLikeDislike[1] ? setDislikesCount(prevState=>prevState-1) : setDislikesCount(prevState=>prevState+1)
            if(userLikeDislike[0])  setLikesCount(prevState=>prevState-1)
            setUserLikeDislike((prevState)=>[false, !prevState[1]])

            }).catch((error)=>{
            setLikeLoading(false)
            setLikingError(error)
            })
        }
    }

    let images_list = []
    if (data){
        let count = 0
        data.images.map(image=>{
            count +=1
            if(count % 6 === 0){
                images_list = [...images_list, 'google-ads']
            }
            images_list = [...images_list, image.url] 
        })
    }
    let phone_number_count = 0
    const {translate} = useContext(TranslationContext)
    return (
        <>  
            { loading ? <ArticleLoading/> : 
            <>
                <section className="p-2 col-12 col-lg-6 mx-auto">
                    <div className='rounded overflow-hidden card'>
                        <div className='d-flex bg-white p-2'>
                            <Link to={`/@${ username }/`} className='image-in-circle mx-2 align-self-start' style={{backgroundImage : `url('${ data.creator_image }')`, width: '50px'}}></Link>
                            <Link to={`/@${ username }/`} >
                                <strong className='mb-0 bolder h6'>{ data.creator_name.text || data.creator_name } {data.creator_name.is_verified && <img width='16' src='/static/others/verified.png'/>}</strong>
                                <TimeElapsed className='text-grey mb-2 small d-block' time={data.created_at} language={language} />
                            </Link>
                            <ArticleParameters isSaver={data.is_saver}/>
                            
                        </div>
                        <ImagesGallery images_list={images_list}/>
                        { likingError && 
                                <Message variant='danger' removeAlert={()=>{setLikingError(null)}} className='m-3'>{ likingError.response.data.detail || likingError.message  }</Message>
                        }
                        <div className='bg-white p-2 position-relative'>
                            <div className='d-flex w-fit-content overflow-hidden'>
                                <button className='d-flex align-items-center py-2 px-3' onClick={likeArticle}  disabled={likeLoading} ><h3>{ likesCount }</h3><i className={` ${userLikeDislike[0] ? 'fa-solid' : 'fa-regular'} ${ !userData.id && 'text-grey' } fa-thumbs-up fa-xl mx-2 cursor-pointer`} width={30}></i></button>
                                <button className='d-flex align-items-center py-2 px-3' onClick={dislikeArticle} disabled={likeLoading}><i className={` ${userLikeDislike[1] ? 'fa-solid' : 'fa-regular'}  fa-thumbs-up fa-rotate-180 ${ !userData.id && 'text-grey' } fa-xl mx-2 cursor-pointer`} ></i><h3>{ dislikesCount }</h3></button> 
                            </div>
                        </div> 
                    </div>
                                       
                </section>
                <section className="p-2 col-12 col-lg-6 mx-auto">
                    <div className="">
                    <div className="justify-content-center">
                        <div className="card p-3">
                            <table className="table mw-500 m-auto">
                                <tbody>
                                <tr>
                                    <th scope="row ">{translate('Category')}</th>
                                    <td className="fw-bold">{ data.info.category_name }</td>
                                </tr>
                                <tr>
                                    <th scope="row ">{translate('Brand')}</th>
                                    <td className="fw-bold">{ data.info.brand_name }</td>
                                </tr>
                                <tr>
                                    <th scope="row ">{translate('Model')}</th>
                                    <td className="fw-bold">{ data.info.title }</td>
                                </tr>
                                <tr>
                                    <th scope="row ">{translate('Year')}</th>
                                    <td>{ data.info.year }</td>
                                </tr>
                                <tr>
                                    <th scope="row ">{translate('Color')}</th>
                                    <td >{ data.info.color_name }</td>
                                </tr>
                                <tr>
                                    <th scope="row ">{translate('Document')}</th>

                                    <td id="document_box">{data.info.document_name}</td>
                                </tr>

                                <tr>
                                    <th scope="row ">{translate('Engine')}</th>

                                    <td id="engine_box">{data.info.engine || <span className='text-danger'>{translate('Not provided')}</span> }</td>
                                </tr>
                                <tr>
                                    <th scope="row">{translate('Car condition')}</th>

                                    <td id="condition_box">{data.info.is_used ? translate('Used') : translate('Never used')}</td>
                                </tr>
                                <tr>
                                    <th scope="row ">{translate('Price')}</th>

                                    <td id="price_box" className={`${ data.info.price === 0 ? 'text-danger':'color-theme fw-bold' }`}>{data.info.price} {translate('Mi')}</td>
                                </tr>
                                <tr>
                                    <th scope="row">{translate('Offered price')}</th>

                                    <td id="offered_price_box" className={`${ data.info.offered_price === 0 ? 'text-danger':'text-grey fw-bold' }`}>{data.info.offered_price} {translate('Mi')}</td>
                                </tr>
                                <tr>
                                    <th scope="row">{translate('Exchange')}</th>

                                    <td id="exchange_box" className='text-grey'>{data.info.exchange ? <span className='color-theme fw-bold'>{translate('Yes')}</span> : <span className='text-danger'>{translate('No')}</span>}</td>
                                </tr>
                                <tr>
                                    <th scope="row">{translate('Mileage')}</th>

                                    <td id="distance_box">{data.info.mileage} {translate('KM')}</td>
                                </tr>
                                <tr>
                                    <th scope="row">{translate('Fuel')}</th>

                                    <td id="fuel_box">{data.info.fuel_name}</td>
                                </tr>
                                <tr>
                                    <th scope="row">{translate('Gearbox')}</th>
                                    <td id="gear_box_box">{data.info.gear_box_name}</td>
                                </tr>
                                <tr>
                                    <th scope="row">{translate('Is it all options?')}</th>

                                    <td id="all_options_box" className='text-grey'>{data.info.is_all_options ? <span className='color-theme fw-bold'>Yes</span> : <span className='text-danger'>{translate('No')}</span>}</td>
                                </tr>
                                <tr>
                                    <th scope="row ">{translate('State')}</th>
                                    <td id="state_box">{data.info.state_name}</td>
                                </tr>
                                <tr>
                                    <th scope="row ">{translate('City')}</th>

                                    <td>{ data.info.city || <span className='text-danger'>{translate('Not provided')}</span> }</td>
                                </tr>
                                </tbody>
                            </table>
                        </div>
                    </div>
                    </div>
                    {!data.info.is_all_options &&
                        <div className="mt-2">
                            <div className="card p-3">
                                <h4 className="color-theme">{translate('Options')}</h4>
                                <div
                                id="options_list"
                                className="d-flex justify-content-center flex-wrap mt-2"
                                >
                                    {data.options.map((option)=>{
                                        return(
                                        <button key={option} className="btn btn-success m-1" >{ option }</button>
                                        )
                                    })}
                                    {data.options.length === 0 ? <h5 className='my-4'>{translate('No option was provided')}</h5> : ''}
                                </div>
                            </div>
                        </div>
                    } 
                    { (userData.id || data.phone_numbers_list.length > 0 )&&  <div className="justify-content-center mt-2" id="phone-number_container">
                        <div className="card p-3">
                            <h4 className='color-theme'>{translate('Contacts')}</h4>
                            <div className='d-flex justify-content-center flex-wrap mt-2'>
                                {userData.id &&
                                    <Link to={'/messages/' + data.creator_id} className="btn btn-success m-2" >
                                        <i className="fa-brands fa-facebook-messenger fa-xl mx-1"></i>
                                        <span className='mx-1'>{translate('Message')}</span>
                                    </Link>
                                }

                                { data.phone_numbers_list.map((number)=>{
                                    return(<button onClick={()=>{window.open('tel:' + number)}} key={phone_number_count +=1} className="btn btn-success m-2" >
                                        <img src="https://img.icons8.com/fluency-systems-regular/50/ffffff/phone-disconnected.png" width={24} />
                                        {number}
                                    </button> )
                                })}
                            </div>
                        </div>
                    </div>}
                    <div className="justify-content-center mt-2">
                    <div className="card p-3" id="description_container">
                        <h4 className="text-fluid color-theme">{translate('Description')}</h4>
                        <div className="p-2">
                        { data.description.length > 0 ? 
                            <p className="check">{ data.description }</p> : 
                            <h5 className='text-center my-4'> {translate('Description was not provided')} </h5>
                        }
                        <p className="check">{  }</p>
                        </div>
                    </div>
                    </div>
                </section>
            </>
            }
        </>
            
    )
}

export default ArticleInfoSection