import axios from 'axios'
import React, { useContext, useEffect, useRef, useState } from 'react'
import { useParams } from 'react-router-dom'
import { useIsVisibleOnce } from '../../utils'
import ArticleCard1 from '../../Components/ArticleCard1'
import ArticleCard1Loading from '../../Components/ArticleCard1Loading'
import { TranslationContext } from '../../store/TranslationContext'

const ArticleOtherArticles = ({brandName, title}) => {
    const simularArticlesSectionRef = useRef();
    const isSimularArticlesSectionVisible = useIsVisibleOnce(simularArticlesSectionRef);
    const { articleSlug } = useParams();
    const slugList = articleSlug.split('-')
    const articleId = slugList[slugList.length - 1]
    const [error, setError] = useState(null)
    const [loading, setLoading] = useState(true)
    const [data, setData] = useState([])
    useEffect(()=>{
        if (isSimularArticlesSectionVisible){
            axios.get(`/api/article/get-simular-articles/?search_text=${brandName + ' ' + title}&search_text_words_list=${JSON.stringify([brandName, title])}&article_id=${ articleId }`).then((response)=>
                {
                    setData(response.data[0])
                    setLoading(false)
                }
            ).catch((error)=>
                {   
                    setError(error)
                    setLoading(false)
                }
            )
        }
        
    }, [articleId, isSimularArticlesSectionVisible])
    const [selectedArticle, setSelectedArticle] = useState(null)
    const {translate} = useContext(TranslationContext)
    return (
        <section className="p-2 col-12 mx-auto">
            <div className=''>
                <h3 className="color-theme px-3 py-4">{translate('Simular articles')}</h3>
                <div className="col-12 d-flex flex-row flex-wrap justify-content-center">
                    { loading ?
                        <>
                            <div ref={simularArticlesSectionRef}/>
                            {Array.from({length: 20}, (_, x) => x).map((i)=>{
                                return(
                                    <ArticleCard1Loading key={i} />
                                )
                            })}
                        </>:
                        data.length === 0 ? 
                            <div className='my-5'><h3 className='my-4 text-center' >{translate('No simular articles')}</h3></div>:
                            data.map(article=>{
                                return(
                                    <React.Fragment key={article.id}>
                                        <ArticleCard1 selectedArticle={selectedArticle} setSelectedArticle={setSelectedArticle} article={article}/>
                                    </React.Fragment>
                                        
                                )
                            })
                    }        
                </div>
            </div>
            
        </section>
    )
}

export default ArticleOtherArticles