import React from 'react'
import { hideUnhide } from '../../../utils'
import { copyText } from '../../../utils'
import { useState } from 'react'
import { useContext } from 'react'
import { UserContext } from '../../../store/UserContext'
import axios from 'axios'
import { useNavigate, useParams } from 'react-router-dom'
import { BrowserContext } from '../../../store/BrowserContext'
import {TranslationContext} from '../../../store/TranslationContext'

const ArticleParameters = ({isSaver}) => {
    const [copied, setCopied] = useState(false)
    const {userData} = useContext(UserContext)
    const {id, token} = userData
    const [saveLoading, setSaveLoading] = useState(false)
    const [saver, setSaver] = useState(isSaver)
    const {articleSlug, username} = useParams()
    const [brand_name, articleTitle, articleId] = articleSlug.split('-') 
    const toggleSaveArticle =()=>{
        setSaveLoading(true)
        axios.post(
            '/api/article/toggle-save-article/',
            {'article-id' : articleId},
            {
                headers : {
                    "Content-Type": "multipart/form-data",
                    Authorization : 'Bearer ' + token
                }
            }
        ).then((response)=>{
            setSaveLoading(false)
            setSaver(response.data)
        }).catch((error)=>{
            setSaveLoading(false)
        })
    }
    const {togglePopup, openReportModal} = useContext(BrowserContext)
    const reportClickHandler =()=>{
        togglePopup()
        openReportModal.current.setData({
            type : 'Article',
            article_id : articleId
        })
        openReportModal.current.openPopUp()
    }
    const navigate = useNavigate()
    const {Right} = useContext(BrowserContext)
    const {translate} = useContext(TranslationContext)
  return (
    <div className="margin-inline-start-auto align-self-center mb-2 position-relative">
        <button className='btn btn-outline-success mx-2' onClick={ ()=>{hideUnhide(`article-parameters`, togglePopup); } }>
            <i className="fa-solid fa-ellipsis fa-2xl">
            </i>
        </button>
            
        <div className='card z-index-1 parameters' id='article-parameters' hidden={true} style={{minWidth: 250, position: 'absolute', [Right]:-2, top:45 }}>
            { username == userData.username && 
                <div onClick={()=>{togglePopup() ; navigate(`/dashboard/your-articles/${articleId}/`)}} className='p-3'><i style={{padding: '4px 10px'}} className="fa-solid fa-pen-to-square rounded mx-1"/>
                    <span className='mx-2'>{translate('Edit article')}</span>
                </div>
            }
            { id && 
            <div onClick={toggleSaveArticle} >
                    <button className='w-100 text-start' disabled={saveLoading}>
                        { !saver && <div className='p-3'><i className="fa-regular fa-bookmark py-1 rounded mx-1" style={{paddingLeft: 12, paddingRight: 12}}></i><span className='mx-2'>{saveLoading ? translate('Saving...') :  translate('Save')}</span></div> }
                        { saver && <div className='p-3'><i className="fa-solid fa-bookmark py-1 rounded mx-1" style={{paddingLeft: 12, paddingRight: 12}}></i><span className='mx-2'>{saveLoading ? translate('Unsaving...') :  translate('Saved')}</span></div> }
                    </button>
                </div>
            }
            <div className='p-3' onClick={()=>copyText(window.location.href, setCopied)}>
                { copied ? <><i className="fa-solid fa-check px-2 py-1 rounded mx-1"></i><span className='mx-2'>{translate('Copied')}</span></>:<><i className="fa-solid fa-link px-2 py-1 rounded mx-1"></i><span className='mx-2'>{translate('Copy link')}</span></>}
                </div>
            <div className='p-3' onClick={ reportClickHandler}><i className="fa-solid fa-exclamation px-3 py-1 rounded mx-1"></i><span className='mx-2'>{translate('Report')}</span></div>
        </div>
    </div>
  )
}

export default ArticleParameters