import React, { useContext, useEffect } from 'react'
import { Link } from 'react-router-dom'
import { BrowserContext } from '../store/BrowserContext'
import { TranslationContext } from '../store/TranslationContext'

const Error404 = () => {
  const {Left} = useContext(BrowserContext)
  const {translate} = useContext(TranslationContext)
  return (
    <div className='text-center py-5 my-5'>
      <div><i className="fa-solid fa-triangle-exclamation text-danger" style={{fontSize:200}}></i></div>
      <h1 className='p-3 text-danger'>{translate('Error 404')}</h1>
      <Link to='/' className='w-fit-content d-flex align-items-center justify-content-center gap-3 btn btn-outline-success m-auto border-0 px-4 py-3' style={{fontSize:30}}><i className={`fa-solid fa-arrow-${Left}`}></i>{translate('Go back home')}</Link>
    </div>
  )
}

export default Error404