import React from 'react'

const ArticleCard1Loading = () => {
  return (
    <div className='col-12 col-sm-6 col-md-4 col-lg-3 p-1'>
        <div className='card'>
            <div className='main-image cursor-pointer mb-0' style={{backgroundColor : 'var(--grey)', width:'100%', minWidth:150}}></div>
            <div className='d-flex flex-column p-2 gap-1'>
                <div className='rounded bg-color-grey' style={{width : '80%', height:20}}/>
                <div className='rounded bg-color-grey' style={{width : '60%', height:20}}/>
                <div className='rounded bg-color-grey' style={{width : '40%', height:20}}/>
                <div className='rounded bg-color-grey' style={{width : '50%', height:20}}/>
                <div className='rounded bg-color-grey margin-inline-start-auto' style={{width : '40%', height:20}}/>
            </div>
            <div className='py-4' />
        </div>        
    </div>
  )
}

export default ArticleCard1Loading