
const ArticleLoading = () => {
  return (
    <div className='d-flex flex-wrap '>
      <section className="p-2 col-12 col-lg-6 mx-auto ">
        <div className="d-flex bg-white px-2 pt-2 rounded-top align-items-center">
          <span className="m-2 rounded-circle" style={{width : '50px', height : '50px', backgroundColor : 'var(--grey)'}}></span>
          <div className="mx-2 d-flex flex-column gap-2">
            <div className='rounded' style={{width : '100px', height : '20px', backgroundColor : 'var(--grey)'}}></div>
            <div className='rounded' style={{width : '150px', height : '20px', backgroundColor : 'var(--grey)'}}></div>
          </div>
        </div>
        <div className="gallery-container">
          <div className='aspect-ratio-1 m-auto w-100 rounded-bottom' style={{width: '100%', position: 'relative', maxHeight: 'calc(100vh - 120px)', backgroundColor : 'var(--grey)'}}/>
        </div>
      </section>
      <section className="p-2 col-12 col-lg-6 mx-auto">
        <div className="card text-black p-3 d-flex flex-wrap flex-row">
        {Array.from({length: 30}, (_, x) => x).map((i)=>{
          return(
            <div key={i} className="col-6">
              <div className="m-3 rounded" style={{backgroundColor : 'var(--grey)', height: '20px'}}></div>
            </div>
          )
        })}
        </div>
        <div className="mt-2">
          <div className="card text-black pb-3 px-2" id="options_container">
            <div className="text-center m-2 p-2">
              <strong>Options</strong>
            </div>
            <div
              id="options_list"
              className="d-flex justify-content-center flex-wrap"
            >
              {Array.from({length: 11}, (_, x) => x).map((i)=>{
            return(
              <button key={i} className="btn m-2" style={{width : '100px', height : '30px', backgroundColor : 'var(--grey)'}}></button>
            )
          })}
            </div>
          </div>
        </div>
      </section>
    </div>
      

  )
}

export default ArticleLoading