import React, { useContext, useEffect, useState } from 'react'
import { forwardRef, useRef, useImperativeHandle } from 'react'
import { hideUnhide } from '../../utils'
import styled from 'styled-components'
import { createPortal } from 'react-dom'
import axios from 'axios'
import { formatDateTimeInLanguage } from '../../utils'
import { Message } from '../Message'
import {Loader} from '../Loader'
import { UserContext } from '../../store/UserContext'
import { Link, useParams } from 'react-router-dom'
import { BrowserContext } from '../../store/BrowserContext'
import TimeElapsed from '../TimeElapsed'
import { TranslationContext } from '../../store/TranslationContext'

const StyledComponent = styled.dialog`
max-width: 800px;
width: -webkit-fill-available;
background-color: transparent;
border: none;
outline: none;
&>div{
    min-height: 200px;
}
`

const CommentModal = forwardRef(({comment, setComments}, ref) =>{
    const [deleteCommentLoading, setDeleteCommentLoading] = useState(false)
    const [errorDeleltingComment, setErrorDeletingComment] = useState(false)
    const username = useParams().username.replace('@', '')

    const ChildRef = useRef()
    const sharedMethods = () =>({ // to execute openPopUp user sharedMethods().openPopUp()
        openPopUp: openPopUp,
    })

    useImperativeHandle(ref, sharedMethods);// use methods and functions of child coponent of thier parent

      const openPopUp = ()=>{
        ChildRef.current.showModal()
    }
    
    const closeDialogHandler = ()=>{
        ChildRef.current.close()
        document.getElementById(`comment-${comment.id}`).setAttribute('hidden', '')
    }
    
    const userData = useContext(UserContext).userData

    const config = {
        headers : {
          "Content-Type": "multipart/form-data",
          Authorization: `Bearer ${userData.token}`
        }
    }
    const deleteComment = ()=>{
        setDeleteCommentLoading(true)
        axios.post(
            '/api/article/delete-comment/',
            {'comment-id' : comment.id},
            config
            ).then((response)=>{
            setComments((prevState)=>{
                return prevState.filter((c)=>c.id !== comment.id)
            });
            closeDialogHandler()
            setDeleteCommentLoading(false)
        }).catch((error)=>{
            setErrorDeletingComment(error)
            setDeleteCommentLoading(false)
            document.getElementById(`comment-${comment.id}`).setAttribute('hidden', '')
        })
        
    }
    const {togglePopup, openReportModal, Right} = useContext(BrowserContext)
    const {language} = useContext(BrowserContext).browserData
    const {translate} = useContext(TranslationContext)
    let JSX =
    <div className='m-auto'>
        <StyledComponent ref={ChildRef} onClose={closeDialogHandler} >
            { comment && 
                <div className='bg-color-white p-3 border rounded d-flex flex-column'>
                    <div className='d-flex align-items-center'>
                        <Link to={`/@${comment.commenter_username}/`} className='d-flex align-items-center'>
                            <div className='image-in-circle mx-2' style={{ width: 40, backgroundImage : `url('${ comment.commenter_image }')` }} ></div>
                            <div className='d-flex flex-column'>
                                <span>{ comment.commenter_name.text || comment.commenter_name} {comment.commenter_name.is_verified && <img width='16' src='/static/others/verified.png'/>}</span>
                                <TimeElapsed className='small text-grey' time={comment.created_at} language={language} />
                            </div>   
                        </Link>
                        
                        <button onClick={ ()=>{hideUnhide(`comment-${comment.id}`, togglePopup)} } className='btn btn-outline-success margin-inline-start-auto '><i className="fa-solid fa-ellipsis fa-2xl" ></i></button>
                    </div>
                    <p className='mt-2 flex-grow-1'>
                        {comment.text}
                    </p>
                    <div className='parameters bg-white border rounded' style={{[Right]: 30, top: 80}} hidden id={`comment-${comment.id}`}>
                        {comment.commenter_id === userData.id || userData.username === username ? 
                            (deleteCommentLoading ?
                                <div className='p-3 d-flex'><span className='mx-1'>{translate('Deleting comment...')}</span></div> :
                                <div className='p-3' onClick={deleteComment}><i className="fa-solid fa-trash px-2 py-1 rounded mx-1"></i><span className='mx-1'>{translate('Delete comment')}</span></div>
                            ) : ''
                        }
                        <div className='p-3' onClick={()=>{ togglePopup(""); openReportModal.current.setData({type:'Comment', comment_id: comment.id}); openReportModal.current.openPopUp()}}><i className="fa-solid fa-exclamation px-3 py-1 rounded  mx-1"></i><span className='mx-1'>{translate('Report')}</span></div>
                    </div>
                    <div className='mt-4'>
                        <form method="dialog" >
                                <button className="btn btn-outline-success px-4">{translate('Exit')}</button>
                        </form>
                    </div>
                    { errorDeleltingComment && <Message removeAlert={()=>setErrorDeletingComment(null)} variant='danger' className='mt-5'>{ errorDeleltingComment.message && errorDeleltingComment.response.data.message }</Message> }
                </div>
            }
        </StyledComponent>
    </div>
        

    return createPortal(JSX, document.getElementById('root'))
})

export default CommentModal