import React, { useContext, useEffect, useRef, useState } from 'react'
import CommentSection from './CommentSection'
import ArticleInfoSection from './ArticleInfoSection'
import Error404 from '../Error404'
import ArticleOtherArticles from './ArticleOtherArticles'
import { useLocation, useParams } from 'react-router-dom'
import { TranslationContext } from '../../store/TranslationContext'
import { BrowserContext } from '../../store/BrowserContext'


const ArticleScreen = () => {
  ///////////////////////////////////
  //
  const[error, setError] = useState(null)
  const [brandName, setBrandName] = useState('')
  const [articleTitle, setArticleTitle] = useState('')
  const {translate} =useContext(TranslationContext)
  const {setTitle, setDescription} = useContext(BrowserContext)
  useEffect(()=>{  
    if( brandName && articleTitle ){
      setTitle(`${ brandName } - ${articleTitle} | ${translate('Ch7al Machya')}`) // Translate this
      setDescription(`${ brandName } - ${articleTitle} ${translate('For sale at Ch7al Machya')}`)
    }
  },[brandName, articleTitle])
  return (
    <>
      { error ? <Error404/> :
        <div className='d-flex flex-wrap'>
            <ArticleInfoSection error={error} setError={setError} setBrandName={setBrandName} setTitle={setArticleTitle} />
            <CommentSection />
            <ArticleOtherArticles brandName={brandName} title={articleTitle} />
        </div>
      }
    </>
      
    )
}

export default ArticleScreen