import React from 'react'
import LazyImage from '../../../Components/UI/LazyImage'
import { useContext } from 'react'
import { BrowserContext } from '../../../store/BrowserContext'
import TimeElapsed from '../../../Components/TimeElapsed'
import { Link } from 'react-router-dom'

const CommentCard = ({comment, setSelectedComment, openCommentModal}) => {
  const openCommentHandler = (comment)=>{
    setSelectedComment(comment)
    openCommentModal.current.openPopUp()
  }

  const {language, togglePopup} = useContext(BrowserContext)
  return (
    <div key={comment.id} className='mx-1 card mb-4 col-10 col-sm-7 col-md-5 col-lg-4 col-xl-3 p-2'>
        <div className='d-flex align-items-center'>
          <Link to={`/@${comment.commenter_username}/`} className='d-flex align-items-center'>
            <LazyImage width={40} className='mx-2 image-in-circle' src={comment.commenter_image}></LazyImage>
            <div className='d-flex flex-column'>
                <strong>{ comment.commenter_name.text || comment.commenter_name} {comment.commenter_name.is_verified && <img width='16' src='/static/others/verified.png'/>}</strong>
                <TimeElapsed className='small text-grey' time={comment.created_at} language={language} />
            </div>
          </Link>
        </div>
        <p className='comment m-2 cursor-pointer' style={{height:72}} onClick={()=>{openCommentHandler(comment); togglePopup('')}}>{ comment.text }</p>
    </div>
  )
}

export default CommentCard